import { useState } from "react";
import "./styles.css";

export default function App() {
  const [hidden, setHidden] = useState(true);

  const handleSpace = (e) => {
    console.log(e);
    if (e.keyCode === 32) {
      setHidden(false);
    }
  };
  return (
    <div
      className="App"
      onClick={() => setHidden(false)}
      tabIndex="0"
      onClick={() => setHidden(false)}
      onKeyDown={handleSpace}
    >
      <div style={{ margin: "auto", width: "20cm" }}>
        <div className="pointWrapper">
          <img
            src="pictures/picture1.jpg"
            className="picture"
            alt=""
            style={{ opacity: 1 }}
          />
        </div>
        {hidden && (
          <div className="pointWrapper">
            <img
              src="pictures/point.png"
              className="point"
              alt=""
              style={{
                borderRight: "1px solid #fff",
                borderBottom: "1px solid #fff"
              }}
            />
            <img
              src="pictures/point.png"
              className="point"
              alt=""
              style={{
                borderTop: "1px solid #fff"
              }}
            />
          </div>
        )}
      </div>
    </div>
  );
}
